import { NgModule } from '@angular/core';
import { StoreModule } from '@ngrx/store';
import { ReCaptchaModule } from 'angular2-recaptcha';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RegisterRoutingModule } from './register-routing.module';
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { AgmCoreModule } from 'angular2-google-maps/core';
import { SharedModule } from './../shared/shared.module';
import { TextMaskModule } from 'angular2-text-mask';
import { SharedLazyModule } from './../shared/shared-lazy.module';
//components
import { FormsComponent } from './forms/forms.component';
import { GeneralInfoComponent } from './forms/general/general.component';
import { SignalitiqueComponent } from './forms/signalitique/signalitique.component';
import { ContactComponent } from './forms/contact/contact.component';
import { BeneficiaireComponent } from './forms/beneficiaire/beneficiaire.component';
import { ResultComponent } from './result/result.component';
import { NavbarFormStepComponent } from './navbar-form-step.component';
import { RibInputComponent } from './rib-input/rib-input.component';
//Directives & pipes
import { RibNumberPipe } from "./rib-input/rib-account-number.pipe";
import { RibNumberFormatterDirective } from "./rib-input/rib-card-number.directive";
// Services & others
import {  signalitique, SignalitiqueService, contact, ContactService, beneficiaire, BeneficiaireService } from './../_state/index';
import{customer,CustomerService} from './../_state/customer';
import{ParametrageService} from './../_services/parametrage.service';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
@NgModule({
  imports: [
    RegisterRoutingModule,
    FormsModule,
    SharedLazyModule,
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyDs3MbHrfAKNiurDnNZ8hIPB7zTS1fOxN8'
    }),
    BsDropdownModule.forRoot(),
    NgbModule.forRoot(),
    CommonModule,
    ReactiveFormsModule,
    ReCaptchaModule,
    StoreModule.provideStore({ customer, signalitique, contact, beneficiaire }),
    StoreDevtoolsModule.instrumentOnlyWithExtension(),
    TextMaskModule
  ],
  declarations: [
    FormsComponent,
    GeneralInfoComponent,
    SignalitiqueComponent,
    BeneficiaireComponent,
    ContactComponent,
    ResultComponent,
    RibInputComponent,
    NavbarFormStepComponent,
    RibNumberFormatterDirective,
    RibNumberPipe

  ],
  providers: [CustomerService,RibNumberPipe, SignalitiqueService, ContactService, BeneficiaireService, ParametrageService]
})
export class RegisterModule { }



