import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { GeneralInfoComponent } from './forms/general/general.component';
import { SignalitiqueComponent } from './forms/signalitique/signalitique.component';
import { ContactComponent } from './forms/contact/contact.component';
import { BeneficiaireComponent } from './forms/beneficiaire/beneficiaire.component';
import { ResultComponent } from './result/result.component';

const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Devenir client Pages'
    },
    children: [
      {
        path: 'infos-generales',
        component: GeneralInfoComponent,
        data: {
          title: 'General informations for customer'
        }
      },
      {
        path: 'signalitique',
        component: SignalitiqueComponent,
        data: {
          title: 'Signalitique informations for customer'
        }
      },
      {
        path: 'contact',
        component: ContactComponent,
        data: {
          title: 'contact informations for customer'
        }
      },
      {
        path: 'beneficiaire',
        component: BeneficiaireComponent,
        data: {
          title: 'beneficiaire informations for customer'
        }
      },
      {
        path: 'result',
        component: ResultComponent,
        data: {
          title: 'result of customer registration'
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RegisterRoutingModule {}



