import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AppStore, SignalitiqueService, SignalitiqueProfile } from './../../../_state/index';
import { Store } from '@ngrx/store';
import { validateDate, validateBirthday } from './date-validator';
import { NgbDatepickerConfig, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { ParametrageService } from './../../../_services/parametrage.service';
import { TranslateService, LangChangeEvent } from '@ngx-translate/core';
@Component({
  selector: 'app-health',
  templateUrl: './signalitique.component.html',
  styleUrls: ['./signalitique.component.css'],
  providers: [NgbDatepickerConfig]
})
export class SignalitiqueComponent implements OnInit {
  form: FormGroup;
  step: number = 2;
  ids: any[];
  constructor(
    public translate: TranslateService,
    public store: Store<AppStore>,
    private signalitiqueService: SignalitiqueService,
    private fb: FormBuilder,
    private router: Router,
    config: NgbDatepickerConfig,
    private parametrageService: ParametrageService
  ) {
    config.minDate = { year: 1920, month: 1, day: 1 };

    this.translate.onLangChange.subscribe((event: LangChangeEvent) => {

      if (this.translate.currentLang === 'fr') {
        this.ids = this.parametrageService.getIds();
      } else {
        this.ids = this.parametrageService.getGbIds();
      }
    });
  }

  ngOnInit() {
    console.log(this.translate.currentLang);
    if (this.translate.currentLang === 'fr') {
      this.ids = this.parametrageService.getIds();
    } else {
      this.ids = this.parametrageService.getGbIds();
    }

    this.signalitiqueService.signalitique$
      .subscribe(this.initForm.bind(this));
  }

  initForm(signalitique: SignalitiqueProfile) {
    this.form = this.fb.group({
      gender: [signalitique.gender, Validators.required],
      birthday: [signalitique.birthday, [Validators.required, validateBirthday]],
      birthplace: [signalitique.birthplace, Validators.required],
      identityDocument: [signalitique.identityDocument, Validators.required],
      identityNumber: [signalitique.identityNumber, Validators.required],
      placeOfIssue: [signalitique.placeOfIssue, Validators.required],
      dateOfIssue: [signalitique.dateOfIssue, Validators.required],
      expirDate: [signalitique.expirDate, [Validators.required, validateDate]],
    });
  }

  back() {
    this.router.navigateByUrl('register/infos-generales');
  }

  next() {
    this.signalitiqueService.updateSignalitique(this.form.value);
    this.router.navigateByUrl('register/contact');
  }
}



