import { Injectable } from '@angular/core';
import { Store, Action } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';
import { AppStore } from './app-store';

const UPDATE_SIGNALITIQUE = 'UPDATE_SIGNALITIQUE';

export interface SignalitiqueProfile {
  gender: string;
  birthday: string;
  birthplace: string;
  identityDocument: string;
  identityNumber: string;
  placeOfIssue: string;
  dateOfIssue: string;
  expirDate: string;
}

const initialState = {
  gender: '',
  birthday: '',
  birthplace: '',
  identityDocument: '',
  identityNumber: '',
  placeOfIssue: '',
  dateOfIssue: '',
  expirDate: ''
};

export const signalitique = (state: SignalitiqueProfile = initialState, action: Action) => {
  switch (action.type) {
    case UPDATE_SIGNALITIQUE:
      return action.payload;
    default:
      return state;
  }
};

@Injectable()
export class SignalitiqueService {
  signalitique$: Observable<SignalitiqueProfile> = this.store.select('signalitique');
  constructor(private store: Store<AppStore>) { }

  updateSignalitique(signalitique) {
    this.store.dispatch({ type: UPDATE_SIGNALITIQUE, payload: signalitique });
  }
}



