import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AppStore, CustomerService, BeneficiaireService, BeneficiaireProfile, ContactService, ContactProfile, CustomerProfile } from './../../../_state/index';
import { Store } from '@ngrx/store';
import { ParametrageService } from './../../../_services/parametrage.service';
declare var google: any;

@Component({
  selector: 'app-beneficiaire',
  templateUrl: './beneficiaire.component.html',
  styleUrls: ['./beneficiaire.component.css']
})
export class BeneficiaireComponent implements OnInit {
  form: FormGroup;
  hidden: boolean = false;
  hiddenBen: boolean = false;
  success: boolean = false;
  customer: any;
  loading: boolean = false;
  step: number = 4;
  lat: number = 48.872418;
  lng: number = 2.318366;
  zoom: number =6;
  email: string;
  label: string = "RIB bénéficiaire";
  agences: any[];
  markers: marker[] = [];
  constructor(
    private store: Store<AppStore>,
    private beneficiaireService: BeneficiaireService,
    private contactService: ContactService,
    private fb: FormBuilder,
    private customerService: CustomerService,
    private router: Router,
    private parametrageService: ParametrageService
  ) {


  }

  ngOnInit() {
    this.agences = this.parametrageService.getAgencesBes();
    this.agences.forEach(element => {
      element.agences.forEach(element => {
        this.markers.push({
          title: element.key,
          lat: element.lat,
          lng: element.lng,
          label: "",
          icon: "assets/img/marker.png",
          draggable: true
        })
      });
    });

    this.beneficiaireService.beneficiaire$
      .subscribe(this.initForm.bind(this));

    this.customer = this.customerService.customer$.subscribe(customer => {
      this.email = customer.email;
    });
    this.contactService.contact$.subscribe(contact => {

      this.initializeMap(contact.cityOfBirth);
    })

  }

  initializeMap(city: string): void {
    city = this.noAccent(city);
    city = city.toLowerCase();
    this.agences.forEach(element => {
      element.agences.forEach(element => {
        let value = element.value.toLowerCase();
        if (value === city) {
          this.form.get('agenceBes').patchValue(element.key);
          // let LatLngList = [];
          // LatLngList.push(new google.maps.LatLng (element.lat,element.lng));
          // console.log(LatLngList);
          this.zoom = 17;
          this.lat = element.lat;
          this.lng = element.lng;
        }
      });
    });
  }
  initForm(beneficiaire: BeneficiaireProfile) {
    this.form = this.fb.group({
      account: [beneficiaire.account, [Validators.required, Validators.pattern(/([0-9]{21})$/)]],
      lastname: [beneficiaire.lastname, Validators.required],
      firstname: [beneficiaire.firstname, Validators.required],
      agenceBes: [beneficiaire.agenceBes, Validators.required],
      newsletterInscription: [beneficiaire.newsletterInscription],
      agree: [beneficiaire.agree],

    });
  }
  hideMap(): void {
    this.hidden = !this.hidden;
  }
  hideBeneficiare(): void {
    this.hiddenBen = !this.hiddenBen;
    if (this.hiddenBen === true) {

      this.customer = this.customerService.customer$.subscribe(customer => {
        this.form.get('lastname').patchValue(customer.lastName);
        this.form.get('firstname').patchValue(customer.firstName);
      });
      this.form.get('lastname').disable();
      this.form.get('firstname').disable();

    } else {
      this.form.get('lastname').patchValue('');
      this.form.get('firstname').patchValue('');
      this.form.get('firstname').enable();
      this.form.get('lastname').enable();
    }
  }

  back() {
    localStorage.removeItem('beneficiaire');
    this.router.navigateByUrl('register/contact');

  }

  next() {
    if (this.hiddenBen === true) {
      this.form.get('firstname').enable();
      this.form.get('lastname').enable();
      this.customer = this.customerService.customer$.subscribe(customer => {
        this.form.get('lastname').patchValue(customer.lastName);
        this.form.get('firstname').patchValue(customer.firstName);
      });
    }
    this.beneficiaireService.updateBeneficiaire(this.form.value);
    this.loading = true;
    localStorage.setItem('beneficiaire', JSON.stringify([{ account: this.form.value.account, firstname: this.form.value.firstname, lastname: this.form.value.lastname }]));
      setTimeout(() => {
        this.router.navigateByUrl('register/result');
     }, 2000);
   
  }

  noAccent(text: string) {
    let accent = [
      /[\300-\306]/g, /[\340-\346]/g, // A, a
      /[\310-\313]/g, /[\350-\353]/g, // E, e
      /[\314-\317]/g, /[\354-\357]/g, // I, i
      /[\322-\330]/g, /[\362-\370]/g, // O, o
      /[\331-\334]/g, /[\371-\374]/g, // U, u
      /[\321]/g, /[\361]/g, // N, n
      /[\307]/g, /[\347]/g, // C, c
    ];
    let noaccent = ['A', 'a', 'E', 'e', 'I', 'i', 'O', 'o', 'U', 'u', 'N', 'n', 'C', 'c'];

    for (let i = 0; i < accent.length; i++) {
      text = text.replace(accent[i], noaccent[i]);
    }
    return text;
  }
  clickedMarker(label: string, index: string) {
    this.setAgenceBes(index);
  }

  setAgenceBes(index: string) {
    this.agences.forEach(element => {
      element.agences.forEach(element => {
        if (element.key === index) {
          this.form.get('agenceBes').patchValue(element.key);
        }
      });
    });
  }
}



interface marker {
  title: string;
  lat: number;
  lng: number;
  label?: string;
  icon?: string;
  draggable: boolean;
}


