import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs/Rx';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Store, Action } from '@ngrx/store';
import { AppStore, CustomerProfile, CustomerService } from './../../../_state/index';
import { Observable } from 'rxjs/Observable';
@Component({
  selector: 'app-user',
  templateUrl: './general.component.html',
  styleUrls: ['./general.component.css']
})
export class GeneralInfoComponent implements OnInit, OnDestroy {
  form: FormGroup;
  step: number = 1;
  hidden: boolean = false;
  success: boolean = false;
  submitted: number = 0;
  error: string = '';
  customer: Observable<CustomerProfile>;
  private subscription: Subscription;
  constructor(
    private store: Store<AppStore>,
    private customerService: CustomerService,
    private fb: FormBuilder,
    private router: Router
  ) { 

  }
  ngOnDestroy() {
    // this.subscription.unsubscribe();
  }
  ngOnInit() {
  this.customerService.customer$
      .subscribe(this.initForm.bind(this));
  }
  ngAfterContentInit() {

  }

  initForm(customer: CustomerProfile) {
    this.form = this.fb.group({
      lastName: [customer.lastName, Validators.required],
      firstName: [customer.firstName, Validators.required],
      email: [customer.email, Validators.compose([Validators.required, Validators.pattern(/^[a-z0-9!#$%&'*+\/=?^_`{|}~.-]+@[a-z0-9]([a-z0-9-]*[a-z0-9])?(\.[a-z0-9]([a-z0-9-]*[a-z0-9])?)*$/i)])],
      confirmEmail: [customer.confirmEmail, Validators.compose([Validators.required, Validators.pattern(/^[a-z0-9!#$%&'*+\/=?^_`{|}~.-]+@[a-z0-9]([a-z0-9-]*[a-z0-9])?(\.[a-z0-9]([a-z0-9-]*[a-z0-9])?)*$/i)])]
    }, { validator: this.matchingPasswords('email', 'confirmEmail') });
  }
  matchingPasswords(passwordKey: string, confirmPasswordKey: string) {
    return (group: FormGroup): { [key: string]: any } => {
      let password = group.controls[passwordKey];
      let confirmPassword = group.controls[confirmPasswordKey];

      if (password.value !== confirmPassword.value) {
        return {
          mismatchedPasswords: true
        };
      }
    }
  }
  handleFormChanges() {
  }

  next() {
    this.customerService.updateUser(this.form.value);
    localStorage.setItem('user', JSON.stringify({ firstname: this.form.value.firstName, lastname: this.form.value.lastName }));
    this.router.navigateByUrl('register/signalitique');
  }
}



