import { Component, Input } from '@angular/core';

@Component({
    selector: 'navbar-step-form',
    template: `<ul id="progressbar">
          <li [class.active]="step == '1'">{{'FORMSTEPS.INFOS' | translate}}</li>
          <li [class.active]="step == '2'">{{'FORMSTEPS.SIGNALITIQUE' | translate}}</li>
          <li [class.active]="step == '3'">{{'FORMSTEPS.CONTACT' | translate}}</li>
          <li [class.active]="step == '4'">{{'FORMSTEPS.BENEFICIAIRE' | translate}}</li>
        </ul>`
})
export class NavbarFormStepComponent {
     @Input() step: string;
    constructor() { }
    ngOnInit(): void {

    }
}



