import { Injectable } from '@angular/core';
import { Store, Action, ActionReducer, combineReducers } from '@ngrx/store';
import { select, compose } from '@ngrx/core';
import { Observable } from 'rxjs/Observable';
import { AppStore } from './app-store';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
const UPDATE_CUSTOMER = 'UPDATE_CUSTOMER';

export interface CustomerProfile {
  firstName: string;
  lastName: string;
  email: string;
  confirmEmail: string;
}

const initialState = {
  firstName: '',
  lastName: '',
  email: '',
  confirmEmail: '',
};


export function customer(state: CustomerProfile = initialState, action: Action): CustomerProfile {
  switch (action.type) {
    case UPDATE_CUSTOMER:
      return action.payload;
    default:
      return state;
  }
};

export const reducers = {
  customer
};

export const rootReducer = compose(combineReducers)(reducers)
export function reducer(state, action) {
  return rootReducer(state, action);
}

@Injectable()
export class CustomerService {

    customer: Observable<CustomerProfile>;
  constructor(private store: Store<AppStore>) {
     this.customer = this.store.select(store => store.customer);
   }
   customer$: Observable<CustomerProfile> = this.store.select('customer').take(1);
  updateUser(customer) {
    this.store.dispatch({ type: UPDATE_CUSTOMER, payload: customer });
  }
}



