import { Injectable } from '@angular/core';
import { CitiesOfAllemagne, IDS,IDSFR, AgencesBES, CitiesOfCHINE, CitiesOfEmiratsArabesUni, CitiesOfEspagne, CitiesOfItalie, CitiesOfRUNI, CitiesOfFrance } from './../_helpers/mock-parametrage';
@Injectable()
export class ParametrageService {

    getMockCitiesOfFrance(): any[] {
        return CitiesOfFrance;
    }

    getMockCitiesOfAllemagne(): any[] {
        return CitiesOfAllemagne;
    }

    getMockCitiesOfChine(): any[] {
        return CitiesOfCHINE;
    }

    getMockCitiesOfEmiratsArabesUni(): any[] {
        return CitiesOfEmiratsArabesUni;
    }

    getMockCitiesOfEspagne(): any[] {
        return CitiesOfEspagne;
    }

    getMockCitiesOfItalie(): any[] {
        return CitiesOfItalie;
    }

    getIds(): any[] {
        return IDS;
    }
    getGbIds(): any[] {
        return IDSFR;
    }
    getAgencesBes(): any[] {
        return AgencesBES;
    }
}


